$celestial-blue: #499fe1ff;
$chrysler-blue: #4824bcff;
$salmon: #e2856eff;
$hookers-green: #517664ff;
$black-olive: #353531ff;


$font-familyCursive: 'Comfortaa', cursive;
$font-familySans: 'Open Sans', sans-serif;
$background: #ebebeb;
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: $font-familySans;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  margin-bottom: 15px;
}

.banner img {
  width: 100%;
  height: auto;
  padding-bottom: 30px;
}

img.logo {
  width: auto;
}

body {
  background: $background;
  //overflow-x: hidden;
  color: $black-olive;

  .RujanVet {

    h1:not(.swot) {
      font-size: 36px;
      font-weight: 700;
      margin-top: 30px;
      margin-bottom: 60px;
      color: $chrysler-blue;
      font-family: $font-familySans;
      text-align: center;
    }

    h2 {
      font-size: 28px;
      font-weight: 700;
      margin-bottom: 30px;
      color: $chrysler-blue;
      font-family: $font-familySans;
      margin-top: 15px;
      text-align: center;
    }

    h3 {
      font-size: 24px;
      font-weight: 700;
      margin-bottom: 30px;
      color: $chrysler-blue;
      font-family: $font-familySans;
      text-align: center;
      margin-top: 15px;
      margin-bottom: 30px;
    }

    h4 {
      font-size: 16px;
      font-weight: 700;
      color: $chrysler-blue;
      font-family: $font-familySans;
      text-align: center;
    }

    .header {
      color: white;
      background: $chrysler-blue;
      font-family: $font-familyCursive;
      padding: 15px 0;
      font-weight: 700;
      border-bottom: 2px solid $celestial-blue;

      &.header-scrolled {
        position: fixed;
        top: 0;
        width: 100%;
        background: $background;
        z-index: 9;
        color: $chrysler-blue;
      }

      .menuItem {
        font-family: $font-familyCursive;
      }
    }


    .banner, img {
      max-width: 100%;
    }

    .badges {

      .badge {
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: center;

        svg {
          margin-right: 10px;
        }

        .swot {
          font-size: 18px;
          font-weight: 700;
          display: inline;
          text-transform: uppercase;
        }
      }
    }

    .contacts {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-end;

      a {
        font-size: 28px;
        font-weight: 700;
      }

      span {
        font-size: 12px;
        font-weight: 500;
      }
    }

    .tarife {
      padding: 60px 0;
      background: $chrysler-blue;

      h3 {
        margin-top: 0;
        color: $background;
      }
      .MuiTypography-caption{
        color: $background;
        font-family: $font-familySans;
      }
    }

    .separator {
      width: 100%;
      display: flex;
      flex-wrap: nowrap;
      align-items: center;

      > * {
        flex-grow: 1;
        min-height: 1px;
        background: $celestial-blue;
      }

      &-text {
        flex: 0 0 40px;
        background: transparent;
        text-align: center;
      }
    }

    .review {
      &__head {
        display: flex;
        justify-content: center;
        margin-bottom: 15px;
      }

      &__item {
        padding: 0 60px 30px;

      }

      &__stars {
        margin-bottom: 15px;
        text-align: center;

        .star {
          color: #fbbc04;
          font-size: 24px;
        }
      }

      &__text {
        max-width: 600px;
        margin: 0 auto;
        display: block;
      }

      &__author {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-left: 15px;

        &_name {
          font-size: 18px;
          color: $chrysler-blue;
        }

        span {
          font-size: 12px;
          align-self: flex-start;
        }
      }
    }

    footer {
      padding-top: 30px;
      background: $chrysler-blue;
      color: white;

      h4 {
        display: flex;
        align-items: center;
        text-transform: uppercase;
        padding-top: 15px;
        margin-bottom: 30px;
        padding-bottom: 15px;
        border-bottom: 1px solid $celestial-blue;
        color: white;
      }


      svg {
        padding-right: 10px;
      }

      a {
        color: white !important;
        text-decoration: none !important;
        display: block;
        clear: both;

      }
    }

    .splide {
      &__pagination__page {
        background: $chrysler-blue;

        &.is-active {
          background: $celestial-blue;
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .hidden-mobile{
    display: none !important;
  }
  .menu{
    margin-top: 10px;
  }

  picture.banner {
    display: block;
    min-height: 100vw;
  }

  .separator {
    margin: 30px 0;
  }
  .footer-separator {
    clear: both;
    width: 100%;
  }
}

@media (min-width: 768px) {
  .banner .img{
    margin-bottom: 60px;
  }
  .contacts-menu{
    display: none !important;
  }
  picture.banner {
    display: block;
    min-height: calc(100vw * (5 / 16));
  }
  .separator {
    margin: 60px 0;
  }
}